@use "base/variables" as v;
@use "base/mixins" as m;

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
  .overlay {
    position: absolute;
    background-color: rgb(117, 0, 250);
    background: linear-gradient(
      7deg,
      rgba(117, 0, 250, 0.5) 0%,
      rgba(171, 52, 172, 0.5) 100%
    );
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  video {
    width: auto;
    display: block;
    @include m.desktop {
      width: 100%;
    }
  }
}

.contenido-video {
  z-index: 10;
  h2 {
    color: v.$blanco;
    font-size: 7rem;
    margin: 0;
  }
  p {
    background-color: v.$rosa;
    display: inline-block;
    padding: 1rem 2rem;
    color: v.$blanco;
    font-weight: 700;
    font-size: 2.4 rem;
    margin: 0;
  }
}
